
html body #root{
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    letter-spacing: 3px;
}
.navlink {
  color : black; 
  font-size: 24px;
  margin-right: 8px;
}

.navlink:hover {
  color: rgb(148, 148, 219);
  font-size: 30px;
}
.foot{
    color: gray;
    font-size: 12px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    margin-left: 2px;
    padding-bottom: 8px;
}
.comment {
    color:black;
    padding: 20px;
    text-align: left;
    margin: 10px;
    margin-top: 25px;
    margin-bottom: 25px;
}

.userpic{
    width: 100px;
    height: 100px;
    border-radius: 50%;
    margin-right: 10px;
}

.author{
    font-size: 26px;
}

.photo{
    max-width:14%;
}

.bodyuser {
    max-width: 70%;
}

.spam {
    max-width: 16%;
}

.spambtn {
    margin: 10px;
    width: 100%;
}

.topicbtn{
    width: 200px;
    margin: 50px;
    margin-bottom: 35px;
    border-radius: 20px;
    border: 3px solid transparent;
}

.title {
    color: black;
    font-size: 80px;
    background-color: rgba(167, 163, 163, 0.671);
}

.subtitle{
    color: black;
    font-size: 20px;
    background-color: rgba(167, 163, 163, 0.863);
}

.head {
    color: black;
    font-size: 40px;
    letter-spacing: 10px;
    margin-left: 40px;
    margin-right: 70px;
    text-orientation: sideways;
    max-height: 600px;
}

.slide {
    width: 100%;
}

.slides {
    max-width: 75%;
}

.codes {
    text-align: left;
}

.homep {
    background-image:url(/sbhacks-v/static/media/home.d841d625.jpg);
    background-size: 100%;
    background-attachment: fixed;
}

.black-b{
    background-color: transparent;
}

.white-t{
    color: black;
}


.full {
    min-height: 100%;
}

.center{
    align-content: center;
    justify-content: center;
    align-items: center;
    justify-items: center;
    text-align: center;
}

.centerself{
    align-self: center;
    justify-self: center;
}
